export function ajaxFormData(formData) {
  return new Promise(function (resolve, reject) {
    axios.post('https://up.qbox.me', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }).then(function (res) {
      resolve(res);
    }).catch(function (err) {
      reject(err);
    });
  });
}