/**
 * url映射规则rule
 * @param {any} rules [h5ToHsq, h5ToIqg, mini, other]
 */
var h5MsfToSchemaMsf = {
  index: {
    target: 'index',
    isTab: true,
    params: {}
  },
  shop: {
    target: 'shop',
    isTab: true,
    params: {}
  },
  message: {
    target: 'message',
    isTab: true,
    params: {}
  },
  profile: {
    target: 'profile',
    isTab: true,
    params: {}
  },
  webview: {
    target: 'webview',
    isTab: false,
    params: {}
  },
  search: {
    target: 'search',
    isTab: false,
    params: {}
  },
  city: {
    target: 'city',
    isTab: false,
    params: {}
  },
  publish: {
    target: 'publish',
    isTab: false,
    params: {}
  },
  login: {
    target: 'login',
    isTab: false,
    params: {}
  },
  setting: {
    target: 'setting',
    isTab: false,
    params: {}
  }
}; // 逆向转换映射规则

function reverseRules(rule) {
  var targetRules = {};
  var hasOwnProperty = Object.prototype.hasOwnProperty;

  for (var key in rule) {
    if (hasOwnProperty.call(rule, key)) {
      var item = rule[key];
      targetRules[item.target] = {
        target: key,
        params: {}
      };

      for (var key2 in item.params) {
        if (hasOwnProperty.call(item.params, key2)) {
          var param = item.params[key2];
          targetRules[item.target].params[param] = key2;
        }
      }
    }
  }

  return targetRules;
}

var schemaMsfToH5Msf = reverseRules(h5MsfToSchemaMsf);
var urlRules = {
  h5MsfToSchemaMsf: h5MsfToSchemaMsf,
  schemaMsfToH5Msf: schemaMsfToH5Msf
};
export default urlRules;